import React, { useState } from 'react';
import { firebaseEnabled, resendVerificationEmail } from '../firebase';

export default function HeaderStatusBanner({ user }) {
  // Hooks must always run
  const [msg, setMsg] = useState('');
  if (!firebaseEnabled || !user) return null;

  // Verified
  if (user.emailVerified) {
    return (
      <div style={{
        background:'#e6ffed',
        border:'1px solid #b2f2c2',
        padding:'4px 8px',
        borderRadius:4,
        fontSize:12,
        margin:'8px 0'
      }}>
        Email verified ✓
      </div>
    );
  }

  // Not verified
  const resend = async () => {
    setMsg('Sending...');
    try {
      await resendVerificationEmail();
      setMsg('Verification email sent. Check your inbox.');
    } catch (e) {
      setMsg(e.message);
    }
  };

  return (
    <div style={{
      background:'#fff3cd',
      border:'1px solid #ffe58f',
      padding:'6px 10px',
      borderRadius:4,
      fontSize:13,
      margin:'8px 0'
    }}>
      <strong>Email not verified.</strong> Please verify.
      <button
        onClick={resend}
        className="btn btn-sm btn-outline-secondary"
        style={{ marginLeft:10 }}
        disabled={msg.startsWith('Sending')}
      >
        Resend
      </button>
      {msg && <div style={{ marginTop:4, fontSize:11 }}>{msg}</div>}
    </div>
  );
}