import React, { useState } from 'react';
import { firebaseEnabled, resendVerificationEmail } from '../firebase';

export default function EmailVerificationBanner({ user }) {
  const [msg,setMsg] = useState('');
  if(!firebaseEnabled || !user || user.emailVerified) return null;

  const resend = async () => {
    setMsg('Sending...');
    try {
      await resendVerificationEmail();
      setMsg('Verification email sent. Check your inbox.');
    } catch(e){
      setMsg(e.message);
    }
  };

  return (
    <div style={{background:'#fff3cd',border:'1px solid #ffe58f',padding:'8px 12px',borderRadius:4,marginBottom:12,fontSize:14}}>
      <strong>Email not verified.</strong> Please click the link sent to {user.email}. 
      <button onClick={resend} style={{marginLeft:10}} className="btn btn-sm btn-outline-secondary">Resend</button>
      {msg && <div style={{marginTop:4,fontSize:12}}>{msg}</div>}
    </div>
  );
}