      dueDate.setDate(dueDate.getDate() + 14); // 14 days rental period
      
      // Update book status to borrowed
      await updateDoc(doc(db, 'books', bookId), {
        status: 'borrowed',
        borrowDate: pickupDate.toISOString(),
        dueDate: dueDate.toISOString()
      });

      // Create transaction record
      const transaction = {
        bookId: bookId,
        requestId: requestId,
        borrowerId: currentUser.uid,
        borrowDate: pickupDate.toISOString(),
        dueDate: dueDate.toISOString(),
        status: 'active',
        rentalAmount: 0, // Will be calculated when returned
        fineAmount: 0,
        type: 'rental'
      };
      
      const transactionRef = await addDoc(collection(db, 'transactions'), transaction);
      
      // Update request with pickup information and transaction reference
      await updateDoc(doc(db, 'requests', requestId), {
        status: 'picked-up',
        pickupDate: pickupDate.toISOString(),
        transactionId: transactionRef.id
      });

      alert(`Book picked up successfully! Due date: ${dueDate.toLocaleDateString()}`);
