      const userOutgoingRequests = requests.filter(req => req.borrowerId === currentUser.uid);
      const outstandingRentFines = userOutgoingRequests
        .filter(request => request.status === 'picked-up')
        .reduce((total, request) => {
          const rentalDetails = calculateRentalDetails({
            ...request,
            bookRentalPrice: request.rentalPrice
          });
          return total + rentalDetails.totalOwed;
        }, 0);

      // Show confirmation dialog with outstanding amounts if any
      let confirmMessage = `Do you want to request to borrow "${book.title}" by ${book.author}?\n\nRental Price: ₹${book.rentalPrice}/day`;
      
