      await createNotification({
        type: 'borrow_request',
        recipientEmail: book.ownerEmail,
        recipientId: book.ownerId,
        senderId: currentUser.uid,
        senderEmail: currentUser.email,
        senderName: userProfile?.name || currentUser.displayName || 'Unknown',
        bookId: bookId,
        bookTitle: book.title,
        bookAuthor: book.author,
        message: `${userProfile?.name || currentUser.displayName || 'Someone'} has requested to borrow your book "${book.title}" by ${book.author}${outstandingRentFines > 0 ? ` (Note: borrower has ₹${outstandingRentFines} in outstanding fees)` : ''}`,
        actionRequired: true
      });
