// Centralized Firebase setup
// Uses modular SDK (v9+). Only initialized if REACT_APP_USE_FIREBASE is truthy.
import { initializeApp } from 'firebase/app';
import { getAuth, onAuthStateChanged, sendEmailVerification } from 'firebase/auth';
import { getFirestore } from 'firebase/firestore';

export const firebaseEnabled = !!process.env.REACT_APP_USE_FIREBASE;

let app = null;
let auth = null;
let db = null;

if (firebaseEnabled) {
  const config = {
    apiKey: process.env.REACT_APP_FB_API_KEY,
    authDomain: process.env.REACT_APP_FB_AUTH_DOMAIN,
    projectId: process.env.REACT_APP_FB_PROJECT_ID,
    storageBucket: process.env.REACT_APP_FB_STORAGE,
    messagingSenderId: process.env.REACT_APP_FB_MSG_SENDER,
    appId: process.env.REACT_APP_FB_APP_ID
  };
  app = initializeApp(config);
  auth = getAuth(app);
  db = getFirestore(app);
}

export { auth, db };

export function observeAuth(callback) {
  if (!firebaseEnabled || !auth) return () => {};
  return onAuthStateChanged(auth, callback);
}

export async function resendVerificationEmail() {
  if (!firebaseEnabled || !auth) throw new Error('Firebase disabled');
  if (!auth.currentUser) throw new Error('No user');
  if (auth.currentUser.emailVerified) return { skipped: true };
  await sendEmailVerification(auth.currentUser);
  return { sent: true };
}